@import url(//db.onlinewebfonts.com/c/1a045963159927274c92b0444fb83c17?family=Avenir);
.layout-wrapper {
  position: relative;
  font-family: 'Avenir Roman';
  height: 100vh;
  width: 100vw;
  overflow: hidden;
}

.layout-wrapper > .page-disable {
  -webkit-animation-name: page-disable;
          animation-name: page-disable;
  -webkit-animation-duration: 0.5s;
          animation-duration: 0.5s;
  border-radius: 0;
}

@-webkit-keyframes page-disable {
  0% {
    border-radius: 48px 0 0 0;
  }

  100% {
    border-radius: 0;
  }
}

@keyframes page-disable {
  0% {
    border-radius: 48px 0 0 0;
  }

  100% {
    border-radius: 0;
  }
}

.page-active {
  overflow: no-content;
  position: absolute;
  left: 0;
  transform: rotate(0deg);
  height: 100vh;
  width: 100vw;
  border-radius: 0px;
  background-color: #FFFFFF;
  z-index: 1000;
}

.page-in {
  -webkit-animation-name: page-in;
          animation-name: page-in;
  -webkit-animation-duration: 1s;
          animation-duration: 1s;
}

.page-out {
  -webkit-animation-name: page-out;
          animation-name: page-out;
  -webkit-animation-duration: 1s;
          animation-duration: 1s;
}

@-webkit-keyframes page-in {
  0% {
    left: 253px;
    transform: rotate(-7deg);
    border-radius: 48px 0 0 0;
    z-index: 0;
  }

  100% {
    left: 0;
    transform: rotate(0deg);
    border-radius: 0;
    z-index: 1000;
  }
}

@keyframes page-in {
  0% {
    left: 253px;
    transform: rotate(-7deg);
    border-radius: 48px 0 0 0;
    z-index: 0;
  }

  100% {
    left: 0;
    transform: rotate(0deg);
    border-radius: 0;
    z-index: 1000;
  }
}

@-webkit-keyframes page-out {
  0% {
    position: absolute;
    left: 0;
    transform: rotate(0deg);
    border-radius: 0;
    z-index: 1000;
  }

  100% {
    position: absolute;
    left: 253px;
    transform: rotate(-7deg);
    border-radius: 48px;
    z-index: 0;
  }
}

@keyframes page-out {
  0% {
    position: absolute;
    left: 0;
    transform: rotate(0deg);
    border-radius: 0;
    z-index: 1000;
  }

  100% {
    position: absolute;
    left: 253px;
    transform: rotate(-7deg);
    border-radius: 48px;
    z-index: 0;
  }
}

.burger-icon-wrapper {
  display: inline-block;
  width: 30px;
  height: 33px;
  cursor: pointer;
}

.burger-icon-container {
  width: inherit;
  height: inherit;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
}

.burger-icon__line {
  width: 30px;
  height: 4px;
  border-radius: 3.5px;
  background-color: #E8E5E5;
}


